import React, { useCallback, useEffect, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { fetchToken, onMessageListener } from './firebaseInit';

function App() {
  const [notification, setNotification] = useState({title: '', body: ''});

  async function postData(url = '', data = {}) {
    const response = await fetch(url, {
      method: 'POST',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json'
        // 'Content-Type': 'application/x-www-form-urlencoded',
      },
      redirect: 'follow',
      referrerPolicy: 'no-referrer',
      body: JSON.stringify(data)
    });
    return response.json();
  }

  useEffect(() => {
    onMessageListener()
      .then(payload => {
        setNotification({title: payload.notification.title, body: payload.notification.body})
      })
      .catch(err => console.log('failed: ', err));
  }, []);

  const subscribe = useCallback(async () => {
    try {
      const token = await fetchToken();
      console.log('token', token);
      if (token) {
        const resp = await postData('http://localhost:3333/api/subscribe', { token, topic: 'notification-system' });
        localStorage.setItem('token', token);
      } else {
        console.log('Permission denied')
      }
    } catch (error){
      console.log(error)
    }
  }, []);

  const unsubscribe = useCallback(async () => {
    try {
      const token = localStorage.getItem('token');
      if (token) {
        const resp = await postData( 'https://powerful-reef-00577.herokuapp.com/api/unsubscribe', { token, topic: 'notification-system' });
        localStorage.removeItem('token');
      }
    } catch (error){
      console.log(error)
    }
  }, []);

  useEffect(() => {
    subscribe();
    return () => {
      unsubscribe();
    }
  }, [subscribe, unsubscribe]);
  return (
    <div className="App">
      <header className="App-header">
        <h1>Welcome to notification-system!</h1>
        <img src={logo} className="App-logo" alt="logo" />
        <p>{notification.title}</p>
        <p>{notification.body}</p>
      </header>
    </div>
  );
}

export default App;
